/*-------------------------------------- 1.1 DEFAULTS AND THEME VARIABLES ----*/

// SASS VARIABLES (To be moved to serparate file)
$mobileWidth: 300px;
$tabletWidth: 768px;
$laptopWidth: 1280px;
$desktopWidth: 1600px;
$xlWidth: 1920px;


:root {

  --hb_black: #333;
  --hb_green: #63CFA7;
  --hb_lightGreen: #b1e7d3;
  --hb_darkGreen: #3f8369;
  --hb_blue: #b1e8f2;
  --hb_red: #fd6a42;
  --hb_orange: #ffb908;
  --hb_white: #fff;
  --hb_lightGrey: #eee;
  --hb_grey: #bbb;
  --hb_darkGrey: #888;

  --hb_background-gradient: radial-gradient(at 50% 100%, #63CFA7 40%, #3AB09E);
	/* Gradient must be in same background-image rule AFTER the svg pattern */
  --hb_hover: 0 0 0 0.25em #63CFA7;
  --hb_shadow: 0 0.25em 0.5em 0.25em #0006,0 0.125em 0.25em 0.125em #0006;
  --hb_shadow-inset: inset -0.1em 0.2em 0.5em 0.2em #000c;

  --hb_slideTime: 0.25s;
  --hb_hoverTime: 0.15s;

	/* layout variables */
  --hb_radius-tiny: 0.15em;
  --hb_radius-small: 0.6em;
  --hb_radius: 1.2em;
  --hb_background: var(--hb_white);
  --hb_border: solid 0.3em var(--hb_lightGrey);
  --hb_gutters: 0.5em;

	/* font stacks */
  --hb_font-secondary: 'Open Sans', Arial, sans-serif;
  --hb_font-primary: Inter, Helvetica, 'Helvetica Neue', Frutiger, Arial, sans-serif;
  --hb_font-fancy: 'Berkshire Swash', 'Brush Script MT', 'Brush Script Std', cursive;

	/* font sizes */
	--hb_font-xxxl: 2.625rem;
	--hb_font-xxl: 2.063rem;
	--hb_font-xl: 1.625rem;
	--hb_font-l: 1.25rem;
	--hb_font-base: 1rem;
	--hb_font-s: 0.813rem;
	--hb_font-xs: 0.7rem;

	/* other font custom props */
	--hb_letterSpacingInterBold: -0.04em;

	/* landing page hidden header offset */
	--hb_landingHeaderOffset: 7.1em;

	/* browser scroll behaviour */
	-ms-scroll-snap-type: y proximity;
	    scroll-snap-type: y proximity;
	scroll-behavior: smooth;

	/* font defaults */
	/* Font dims set on root don't get scaled by component level font size, apart from font. */
	/* I don't know why, but that's the behaviour in Chrome currently. */
	/* Only set styles here, no dimensional props like line-height, or letter-spacing. */

	color: var(--hb_black);
	font-weight: 400;
	font-family: var(--hb_font-primary);

	/* fluid root units (14px @ 768 - 18px @ 1920) */
	font-size: clamp(0.875rem, calc(0.875rem + ((1vw - 0.48rem) * 0.3534)), 1.125rem);

	/* Scrollbar width updated by js function */
	--scrollbar: 0px;

	/* SVG border images */
