	display: flex;
	position: relative;
	align-items: center;
	justify-content: center;
	box-shadow: var(--hb_shadow);
	border-radius: var(--hb_radius);
	width: auto;
	height: 80vmin;
	overflow: hidden;
}

.animation_title {
	position: absolute;
	top: 6%;
